import 'material-design-icons-iconfont/dist/material-design-icons.css'

<template>
  
  <div class="pa-3" id="tester">

      <v-dialog   v-model="uploadingDialog"  persistent width="300">
    
           <v-card  elevation="16" class="mx-auto" min-width="300">
                  <v-card-title class="primary white--text">Uploading Meter Read</v-card-title>
                  <v-container style="height: 200px;">
              
                    <v-card-text>Percentage: {{  uploadedPercent }}</v-card-text>
                    <v-card-text>Total:      {{  uploadedTotal   }}</v-card-text>
                    <v-card-text>Loaded:     {{  uploadedLoaded  }}</v-card-text>

                  </v-container>
            </v-card>

      </v-dialog>
 
      <v-dialog  v-model="previousMeterDialog">
    
                <v-card>
              
                  <v-card-title>
                     <span class="text-h5">Previous Meter</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>
                      <v-row>

                        <v-col cols="12" sm="6">
                          <v-text-field dense  outlined label=  "Meter Identifier"  hint = "Meter Identifier, ie Serial Number" readonly></v-text-field>
                          <v-text-field dense  outlined label=  "Meter Value"       hint = "Last Meter Read" readonly></v-text-field>
                          <v-text-field dense  outlined label=  "Meter Metric"      hint = "Meter Metric" readonly></v-text-field>
                          <v-text-field dense  outlined label = "Meter Read Date"   hint = "Date of read" readonly></v-text-field>
                          <v-textarea   dense  outlined label = "Comments:"></v-textarea>
                        </v-col>
                        
                        <v-col class="myborder" cols="12" sm="6">

                            <v-img  
                                  max-height="80vh"
                                  contain
                                  :src          = "meterImage" 
                                  @click        = "showLightBoxImage()"
                            >
                            </v-img>

                        </v-col>

                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                     <v-spacer></v-spacer>
                     <v-btn color="blue darken-1" text  @click ="previousMeterDialog = false">Close</v-btn>
                  </v-card-actions>

              </v-card>

      </v-dialog>

      <vue-easy-lightbox 
            :visible  = "lightBoxVisible" 
            :imgs     = "lightBoxImage"
            @hide     = "hideLightBoxImage"
      ></vue-easy-lightbox>
      
      <label class="d-none">
          <input id="file-input"         v-on:change="imageChange"   type="file" accept="image/*" capture >
      </label>
      
      <label class="d-none">
           <input id="file-input_gallery" v-on:change="imageChangeGallery" type="file" accept="image/x-png,image/jpeg,image/gif">
      </label>
      
      <v-row class="pa-2 mb-2 d-flex justify-space-between align-baseline">
                
            <div>Engineer Meter Reading</div>
            <div>   

              <v-btn  :loading = "loading" @click="storeMeterRead" class="mx-2" fab medium color="success">
                <v-icon dark>mdi-upload-box</v-icon>
              </v-btn>

              <v-btn  :loading = "loading" @click="showPreviousMeter" class="mx-2" fab medium color="error">
                <v-icon dark>mdi-page-previous</v-icon>
              </v-btn>


            </div>

      </v-row>

      <v-row >
            <v-expansion-panels class="expansion_panels">
          
              <!-- customerComponent -->
                <customer v-bind:customer = "customer" />
              <!-- customerComponent -->

                <!-- Previous Meter Reading 
                <v-expansion-panel class="expansion_pane mb-1"> 
                <v-expansion-panel-header>Previous Meter Read</v-expansion-panel-header>
                <v-expansion-panel-content>

                        <div class="row">
                        
                          <div class="col" cols="12" sm="6">
                                  <v-text-field   readonly dense hint="Meter Identifier"      outlined v-model="previousReading.meter_id"         label="Meter Identifier"></v-text-field>
                                  <v-text-field   readonly dense hint="Meter Metric"          outlined v-model="previousReading.meter_metric"     label="Meter Metric"></v-text-field>
                                  <v-text-field   readonly dense hint="Meter Reading Date"    outlined v-model="previousReading.created_at"       label="Meter Date"></v-text-field>
                                  <v-text-field   readonly dense hint="Meter Reading"         outlined v-model="previousReading.meter_value"      label="Meter Value"></v-text-field>
                          </div>

                          <div class="col dcontents" cols="12" sm="6">

                                          <v-card class="mx_auto" max-width="400">

                                          <v-card-title class="pt-2">Prior Meter Image</v-card-title>
                                          <v-card-text class="d-flex align-center">

                                          <v-img
                                                contain
                                                :src          = previousReading.meter_image
                                                @click        = "showLightBoxImage()"
                                                width         = "300px"
                                                >
                                          </v-img>
                                          </v-card-text>
                                          </v-card>
      
                          </div>
                        
                        </div>

                        <div class="row">
                            <div class="col mt-1 pa-1" cols="12">
                                  <v-textarea     readonly outlined v-model=  "previousReading.comments" label="Comments:"></v-textarea>
                              </div>
                        </div>

                </v-expansion-panel-content>
                </v-expansion-panel>
               -->


              <!-- Current Meter Reading -->
                 <v-expansion-panel class="expansion_pane mb-1"> 
                  <v-expansion-panel-header>
                    <div>Meter Read</div>
                  </v-expansion-panel-header>

                  <v-expansion-panel-content>

                    <v-row>
                        
                        <v-col cols="12" sm="6">
                                <v-text-field   clearable dense hint="Meter Identifier"      outlined v-model="meterReading.meter_id"         label="Meter Identifier"></v-text-field>
                                <v-text-field   type="number" clearable dense hint="Meter Reading"         outlined v-model="meterReading.meter_value"      label="Meter Value"></v-text-field>
                                <v-autocomplete
                                         required
                                         outlined
                                         clearable
                                         dense
                                         hint="Meter Metric"
                                         v-model="meterReading.meter_metric"
                                         :items="['Meters', 'Feet']"
                                         label="Meter Metric"
                                ></v-autocomplete>
                                <v-textarea  dense outlined v-model="meterReading.comments" label="Comments:"></v-textarea>

                        </v-col>

                        <v-col  class="myborder pa-1" cols="12" sm="6">
         

                                          <v-img 
                                                contain
                                                :src          = "meterImage" 
                                                @click        = "showLightBoxImage()"
                                                >
                                          </v-img>
            

                                    <v-divider class="mt-4 border-opacity-25"></v-divider>
                                           
                                    <v-card-actions>
                                               <v-btn class="text-none" text color="primary" @click="takeImageCamera()">Camera
                                               </v-btn>
 
                                               <v-btn class="text-none" text color="primary" @click="takeImageGallery()">Gallery
                                               </v-btn>
                                                                 
                                               <v-spacer></v-spacer>
 
                                               <v-btn class="text-none" text color="warning" @click="resetImage()">Reset
                                               </v-btn>
 
                                    </v-card-actions>      
                        </v-col>
                      
                    </v-row>


                  </v-expansion-panel-content>
                </v-expansion-panel>
              <!-- Current Meter Reading -->


              <!-- last one for space-->
                <v-expansion-panel class="expansion_panel mb-2"> 
                </v-expansion-panel>
              <!-- last one for space-->

            </v-expansion-panels>

     </v-row>

  </div>

</template>

<script>


import {store}   from '../../main.js'
import Localbase from 'localbase'

export default {

   name     : "meterRead",
   formName : 'meterRead',

    destroyed () {
       
          store.consoleLog( "meterRead form destroyed");

           this.customer            = JSON.parse( localStorage.getItem( "equalResponseCustomer") );
           this.engineer            = JSON.parse( localStorage.getItem( "equalResponseEngineer") );
           this.meterReading.engineer_id      = this.engineer['ENGINEER_ID'];
           this.meterReading.customer_id      = this.customer['ACCOUNT'];

           this.createSnapShot();
    },
      
    created    (){ 
 
         store.consoleLog( "meterRead form created");

         this.customer                    = JSON.parse( localStorage.getItem( "equalResponseCustomer") );
         this.engineer                    = JSON.parse( localStorage.getItem( "equalResponseEngineer") );
         this.meterReading.engineer_id    = this.engineer['ENGINEER_ID'];
         this.meterReading.customer_id    = this.customer['ACCOUNT'];
         this.restoreSnapShot();
    }, 


   methods   : {

              showPreviousMeter() {
                  this.previousMeterDialog = true;
              },


              storeMeterRead() {

                  const meterURL  =  this.$restServer.meter;
                  this.loading    = true;

                  let   formData     =  new FormData();
                  formData.append( 'meterImage',     this.meterImage);
                  formData.append( 'meterReading',   JSON.stringify(this.meterReading) );
                  

                  this.uploadingDialog = true;
                  this.$http.post(meterURL, formData, {
                 
                        headers          : { 'Content-Type': 'multipart/form-data'},

                        onUploadProgress : ( progressEvent ) => {
                           this.uploadedPercent          = parseInt(Math.round( (progressEvent.loaded / progressEvent.total) * 100));
                           this.uploadedTotal            = progressEvent.total;
                           this.uploadedLoaded           = progressEvent.loaded;
                       }   
                 
                  }).then(  (response) => {
                 
                     this.loading    = false;
                     this.uploadingDialog = false;
                     this.previousReading = response.data;

                  }).catch( (e) => { 
                    this.loading          = false;
                    this.uploadingDialog  = false;
                     alert(e);
                  });

              },

              getLatestMeterRead() {
                const meterURL  =  this.$restServer.meter;
                this.$http.get( meterURL )
                .then(  (response) => { 
                         console.log(response);
                })
                .catch( (e)        => {
                         store.consoleLog(e); 
                });
              },

              createSnapShot(){

                let snapShot             =  this.generateSnapShot();
                let db                   =  new Localbase( this.$dbName );

                db.collection( this.collection ).doc( this.meterReading.customer_id).set( snapShot )
                .then(response => { 
                   console.log( response)
                })
                .catch(error => {
                   console.log( error)
                })
              },

              restoreSnapShot(){

                let db = new Localbase(  this.$dbName );
                db.collection( this.collection).doc( this.meterReading.customer_id).get()
                .then( document => {
                  if ( document != null ) {
                         this.meterReading        = document['meterReading'];
                         this.previousReading     = document['previousReading'];
                         this.meterImage          = document['meterImage'];
                         
                  }
                })
                .catch( error => {
                   console.log( error)
                });

              },

              generateSnapShot() {
                                let snapShot          =   { 
                                                             'meterReading'        : this.meterReading,
                                                             'previousReading'     : this.previousReading,
                                                             'meterImage'          : this.meterImage,

                                                          };
                                return snapShot;
              },
    
              showUploadingDialog() {
                    this.uploadingDialog.showDialog       = true;
              },
    
              closeUploadingDialog() {
                          setTimeout( () => {
                              this.uploadingdDialog.showDialog          = false;
                          }, 2000)
              },
 
              takeImageGallery     : function() {
                    document.getElementById('file-input_gallery').click();
              },

              takeImageCamera      : function ()  {
                    document.getElementById('file-input').click();
              },

              resetImage           : function ()  {

                    this.meterImage  = this.$restServer['blankPhoto'];
                    this.createSnapShot();

              },

              imageChangeGallery    : async function() {
                                          
                                          try {
                                         
                                              var file     = document.getElementById("file-input_gallery");
                                              if (file.files.length > 0 ) {
                                               
                                                  const photo              = document.getElementById("file-input_gallery").files[0];
                                                  const dataUrl            = await this.getDataUrl( photo  );
                                                  this.meterImage          = dataUrl;
                                                  this.createSnapShot();
                                                  document.getElementById('file-input_gallery').value = null;
                                              }
                      
                                          }catch (error) {
                                              console.log(error); 
                                          }
                         
              },
                      
              imageChange           : async function() {
                                          
                    try {
                   
                        var file     = document.getElementById("file-input");
                        if (file.files.length > 0 ) {
                         
                            const photo            = file.files[0];
                            const dataUrl          = await this.getDataUrl( photo );
                            this.meterImage        = dataUrl;
                            this.createSnapShot();
                            document.getElementById('file-input').value = null;
                        }

                    }catch (error) {
                        console.log(error); 
                    }
   
              },
           
              getDataUrl           : function ( photo) {

                        return new Promise(function(resolve, reject) {
                          
                          const reader = new FileReader();
                          reader.readAsDataURL(photo);
                          
                          reader.addEventListener('load',  function(e) {
                              const dataUrl = e.target.result; 
                              resolve( dataUrl );
                          });

                          reader.addEventListener('error', function(e) {
                             reject(e);
                          });

                        });
              },

              showLightBoxImage     : function () {
                    this.lightBoxImage          = this.meterImage;
                    this.lightBoxVisible        = true
              },
     
              showPreviousLightBoxImage    : function () {
                    this.lightBoxImage          = this.previousMeterImage;
                    this.lightBoxVisible        = true
              },
     
              hideLightBoxImage    : function() {
                    this.lightBoxImage          =  null;
                    this.lightBoxVisible        =  false
              },

   },

   data() {

    return {
        
         MySnackBar        : { "show" : false, "text" : "Default Text", "showImage" : "icon"},
       
         uploadingDialog          :  false,
         previousMeterDialog      :  false,

         uploadedPercent          :  0, 
         uploadedTotal            :  0, 
         uploadedLoaded           :  0,

        loading           : false,
        lightBoxVisible   : false,
        lightBoxImage     : null,
        formName          : "meterRead",
        
        customer          : {},
        salesman          : {},
        engineer          : {} ,

        collection        :  "meterRead",
                

        previousReading          : {
                                      engineer_id       :  "",
                                      customer_id       :  "",
                                      meter_id          :  "",
                                      meter_value       :  "",
                                      meter_metric      :  "",
                                      meter_image       :  "",
                                      comments          :  "",
                                      created_at        :  "",
                                      updated_at        :  "",
        },


        meterImage       :  this.$restServer['blankPhoto'],
        meterReading     :  {
                              engineer_id      :  "",
                              customer_id      :  "",
                              meter_id         :  "",
                              meter_value      :  "",
                              meter_metric     :  "",
                              comments         :  "",
                            },

        };
  }

};


</script>

<style scoped>

.mw400{
  max-width: 400px !important;
}

.myborder {
  border        : 1px solid black;
  border-radius : 4px !important;
}

.row {
  display: flex; /* equal height of the children */
}

.col {
  flex: 1; /* additionally, equal width */
}

.dcontents{
  display: contents !important;
}

h1,h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.dl_expansion_panel_content>>> .v-expansion-panel-content__wrap {
  padding: 0 !important;
}

.header{
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: bold;
}

input[type=text] {
  width: 100%;
  padding: 8px 20px;
  margin: 2px 0;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}

input[type=text]:focus {
  border: 3px solid #555;
}

div.v-text-field__details {
  display: none;
}

legend.v-label {
  font-size: 16px !important;
}

.fill {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden
}
.fill img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%
}


</style>
       
